import React, { Component } from 'react';
import { Col, Row } from 'reactstrap';
class Header extends Component {
  render() {
    return (
      <Row className="header">
        <Col>
          <h1>John Michael Ferraris</h1>
          <p style={{ color: 'grey' }}>{new Date().getFullYear() - 2013} years in the industry</p>
        </Col>
        <Col md="auto">
          <a href="https://github.com/jhnferraris" target="_blank" rel="noopener noreferrer">
            <img className="github-link" src={require('../styles/images/privateinvestocat.ico')} alt="github link" />
          </a>
          <a href="https://www.linkedin.com/in/johnferraris/" target="_blank" rel="noopener noreferrer">
            <img className="linkedin-link" src={require('../styles/images/linkedin.png')} alt="linkedin link" />
          </a>
        </Col>
      </Row>
    );
  }
}

export default Header;



